<template>
  <div class="quiz-item">
    <div class="quize-wrap">
      <div class="quize-content-wrap">
        <div class="quize-user">{{ getQuizAuthor }}</div>
        <div class="quize-content">
          <div class="quize-title">
            {{ getQuizTitle }}
          </div>

          <div class="quize-answers">
            <div class="quiz-answer-wrap" v-if="!clickAnswer">
              <div class="quize-answer" @click="quizeAnswer(1)">
                <div class="outter">
                  <div class="inner">
                    {{ getQuizAnswers[0] }}
                  </div>
                </div>
              </div>
              <div class="quize-answer" @click="quizeAnswer(2)">
                <div class="outter">
                  <div class="inner">
                    {{ getQuizAnswers[1] }}
                  </div>
                </div>
              </div>
              <div class="quize-answer" @click="quizeAnswer(3)">
                <div class="outter">
                  <div class="inner">
                    {{ getQuizAnswers[2] }}
                  </div>
                </div>
              </div>
              <div class="quize-answer" @click="quizeAnswer(4)">
                <div class="outter">
                  <div class="inner">
                    {{ getQuizAnswers[3] }}
                  </div>
                </div>
              </div>
            </div>
            <div class="quize-pass" v-if="clickAnswer">
              <div class="outter">
                <div class="inner">
                  <div v-if="!getExlanCheck">
                    <h1 class="pass-title">
                      <span v-if="pass">!</span>
                      <span v-if="!pass">!</span>
                      <!--<span v-if="getIsAuth && pass">+{{ getQuizPoint }}</span>-->
                    </h1>
                    <div class="quize-login-wrap">
                      <a
                        href="javascript:void(0)"
                        class="quize-login"
                        @click="goLogin"
                        v-if="!getIsAuth"
                      >
                        <span v-if="pass">  !</span>
                        <span v-if="!pass">  !</span>
                      </a>
                      <a
                        href="javascript:void(0)"
                        class="quize-login"
                        @click="showExplan"
                        v-if="getIsAuth"
                      >
                        <span v-if="pass"> !</span>
                        <span v-if="!pass"> !</span>
                      </a>
                    </div>
                  </div>
                  <div class="quiz-explanation" v-if="getExlanCheck">
                    <div v-html="getQuizExplanation"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "QuizItem",
  props: {
    quizID: String
  },
  data() {
    return {
      quizInfo: null,
      rightAnswer: 1,
      pass: false,
      clickAnswer: false,
      explanCheck: false
    };
  },
  created() {
    if (this.quizID != undefined) {
      this.getQuizInfo();
      this.checkAnswer();
    }
  },
  watch: {
    quizID() {
      if (this.quizID != undefined) {
        this.getQuizInfo();
        this.checkAnswer();
      }
    }
  },
  computed: {
    ...mapGetters(["getUser"]),
    getIsAuth() {
      return this.getUser.isAuth;
    },
    getExlanCheck() {
      return this.explanCheck;
    },
    getQuizTitle() {
      if (this.quizInfo == null) {
        return "    ?";
      }
      return this.quizInfo.title;
    },
    getQuizAnswers() {
      if (this.quizInfo == null) {
        return ["", "", "", " "];
      }
      return this.quizInfo.answers;
    },
    getQuizRIghtAnswer() {
      if (this.quizInfo == null) {
        return "";
      }
      return this.quizInfo.rightAnswer;
    },
    getQuizExplanation() {
      if (this.quizInfo == null) {
        return " (-, : law of universal gravity)       .     1687     ,  (Principia)    .     2      ,        .       ,   ,  ,            .     (universal force)   .";
      }
      return this.quizInfo.explanation;
    },
    getQuizParticipantCnt() {
      if (this.quizInfo == null) {
        return 0;
      }
      return this.quizInfo.participantCnt;
    },
    getQuizPoint() {
      if (this.quizInfo == null) {
        return 0;
      }
      return this.quizInfo.point;
    },
    getQuizUploadDate() {
      if (this.quizInfo == null) {
        return "00.00.00";
      }
      return (
        this.quizInfo.uploadDate.year +
        "." +
        this.quizInfo.uploadDate.month +
        "." +
        this.quizInfo.uploadDate.day
      );
    },
    getQuizAuthor() {
      if (this.quizInfo == null) {
        return "";
      }
      return this.quizInfo.author;
    }
  },
  methods: {
    ...mapActions(["quizGet"]),
    checkAnswer() {},
    quizeAnswer(answer) {
      this.clickAnswer = true;
      if (answer === this.rightAnswer) {
        this.pass = true;
      } else {
        this.pass = false;
      }
    },
    async getQuizInfo() {
      if (this.quizID != undefined) {
        const res = await this.quizGet({ quizID: this.quizID });
        this.quizInfo = res;
      }
    },
    goLogin() {
      this.$router.push("login");
    },
    showExplan() {
      if (!this.pass) {
        this.clickAnswer = false;
        return;
      }
      this.explanCheck = true;
    }
  }
};
</script>

<style lang="scss">
@import "@/styles/gwahangmi/quiz/quiz-item.scss";
</style>
